@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@100;300;400;700&display=swap);
header{
    background-color: var(--color-blue);
    width: 100%;
    color: white;
    display: flex;
}

.data{
    font-size: 1rem;
    font-weight: normal;

}

.temperatura{
    text-align: center;
    color: white;
    padding: 1.5rem;
}

.temperatura .localidade{
    height: 3rem;
    width: 3rem;
    margin: auto;
}

.temperatura h1{
    font-size: 2rem;
    font-weight: 600;
    text-transform: uppercase;
    line-height: .5rem;
    letter-spacing: .2rem;
    padding-bottom: 1.5rem;
}

.temperatura .temperatura-info h2{
    font-size: 1.6rem;
    font-weight: normal;
    padding-bottom: 1.5rem;
}
.temperatura .temperatura-info p{
    font-size: 1.3rem;
}
main{
    font-size: 1rem;
    font-weight: normal;
    padding: 1rem;
    display: grid;
    grid-template-columns: repeat (4, 1fr);
    grid-gap: 1rem;
}

.semana{
    display: grid;
    grid-template-columns: auto auto auto auto;
    text-align: center;  
}

.temperatura h1 {
    font-size: 1.5rem;
    font-weight: 600;
    text-transform: uppercase;
    line-height: .5rem;
    letter-spacing: .2rem;
    text-align: center;
}

.temperatura h2 {
    font-size: 1.5rem;
    text-align: center;
   
}

.temperatura p {
    font-size: 1rem;
   text-align: center;
}

.temperatura h3 {
    font-size: 1rem;
    font-weight: 600;
    text-transform: uppercase;
    line-height: .5rem;
    letter-spacing: .2rem;
    text-align: center;
    width: 100%;
    padding-bottom: 1rem;
}

.temperatura h4 {
    font-size: 1rem;
    font-weight: normal;
    line-height: .5rem;
    letter-spacing: .1rem;
    text-align: center;
    padding-bottom: .5rem;
}

.temperatura .temperatura-info p {
    font-size: 1rem;
}

img{
    width: 4rem;
    height: 5rem;
    padding-bottom: 1rem;
}

@media screen and (max-width: 769px){
    .semana{
        grid-column: span 1;
    }

    img{
        width: 8rem;
        height: 9.5rem;
        padding-bottom: .5rem;
        padding-top: 2rem;
    }

    .temperatura h3 {
        margin-top: 15rem;
    }


    .temperatura h1{
        margin-top: 15rem;  
    }
}

*{
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

:root{
    --color-blue: #82C5C1;
    font-size: 1.5vw;
}

html, body{
    width: 100%;
    height: 100%;
    background-color: #82C5C1;
    background-color: var(--color-blue);
    font-family: 'Barlow', sans-serif;
}

@media screen and (max-width: 769px){
    :root{
        font-size: 2.1vw;
    }  
}
